import logo from '~/static/images/logoempresa.png'
import brand from '~/static/text/brand'
import link from '~/static/text/link'
import Hidden from '../Hidden'
import { mapState } from 'vuex'
import axios from 'axios'
export default {
  components: {
    Hidden
  },
  data() {
    return {

      TitleForm:'Dejanos tus datos',
      Subtitle:'¿Tiene usted alguna pregunta?  ¿O simplemente quieres saludar? Envíanos un mensaje.',
     modelo:{
       nombre:'',
       correo:'',
       mensaje:''
     },
      valid: true,
      snackbar: false,
      name: '',
      nameRules: [v => !!v || 'Name is required'],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
      ],
      phone: '',
      company: '',
      message: '',    
      logo: logo,
      brand: brand,
      routeLink: link
    }
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true
        this.EnviarMensaje();
      }
    },
    EnviarMensaje(){
       let me = this;
      const data = me.modelo;     
      let url = me.url_base+"Control/Mensaje.php";    
      axios({
        method: "POST",
        url: url,
        data: data,
      })
        .then(function(response) {
        //  alert("Enviado");
         me.limpiar();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    limpiar(){
          this.modelo.nombre='';
       this.modelo.correo='';
        this.modelomensaje='';
     },
    
    
  },
  computed: {
      ...mapState(['url_base']),
    isMobile() {
      const xsDown = this.$store.state.breakpoints.xsDown
      return xsDown.indexOf(this.$mq) > -1
    }
  }
}
